import React from 'react'

const PageSelector = (props) => {
    if (props.tweetsLength === 0) {
        return null
    }

    return (
    <div>
        <button onClick={props.handlePageDown} type="submit">Page Down</button>
        {props.page}
        <button onClick={props.handlePageUp} type="submit">Page Up</button>
    </div>
    )
}

export default PageSelector